import IAction from '../Actions/IAction';
import BaseStore from './BaseStore';

import ManifestStore, { IManifestStoreEntry } from './../../ManifestStore';

import SelectManifestAction from './../Actions/SelectManifestAction';

export class ManifestManagerStore extends BaseStore {

  private static _instance: ManifestManagerStore;

  public static get instance(): ManifestManagerStore {
    if (!this._instance) {
      this._instance = new ManifestManagerStore();
    }

    return this._instance;
  }

  public get registeredManifests(): { [id: string]: IManifestStoreEntry } {
    return ManifestStore.instance.getManifestMap();
  }

  public get selectedManifestId(): string {
    return this._selectedManifestId;
  }

  private _selectedManifestId: string;

  constructor() {
    super();
    this.addProcessActionCallback(this.processAction);
  }

  /**
   * Processes the action fired
   */
  public processAction(action: IAction): void {
    if (action instanceof SelectManifestAction) {
      this._selectedManifestId = action.selectedManifestId;
      this.emitChange();
    }
  }
}

// instantiate the store so it can start listening to dispatch events
/* tslint:disable:export-name */
export default ManifestManagerStore.instance as ManifestManagerStore;
/* tslint:enable:export-name */